

body {
    font-family: 'Lato', sans-serif;
}

.wrapper {
    position: relative;
    height: 35px;
    width: 35px;
    z-index: 1200;
    background: transparent;
}

.icon {
    width: 35px;
    height: 35px;
    border: 3px solid transparent;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
}

.icon svg {
    width: 30px;
    position: absolute;
    top: calc(50% - 15px);
    left: calc(50% - 15px);
    transform: rotate(180deg);
    fill: #F5A629;
}

