$plex: 'Source Code Pro', monospace;
$background: #fff;
$shadow: 0 4px 12px -2px rgba(#6B75A1, .16);
$ease: all 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55);

body {
  font-family: $plex;
  color: #ffffff;
}

.create_btn {
  background: #323232;
  border: none;
  width: 150px;
  cursor: pointer;
  height: 30px;
  border-radius: 2px;

  p {
    color: white;
  }


  &:hover {
    background-color: #F5A629;
  }
}


a {
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
}
