body {
    background-color: #100e17;
    font-family: 'Open Sans', sans-serif;
}

.container {
    position: relative;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 15px;
    display: flex;
    z-index: 800;
}

h4 {
    position: absolute;
    bottom: 50px;
    left: 34px;
    z-index: 200;
    pointer-events: none;
}

h3 {
    overflow-wrap: anywhere;
    -webkit-font-smoothing: antialiased;
}

.card {
    cursor: pointer;
    display: flex;
    height: 219px;
    width: 200px;
    background-color: #1a1a1a;
    border-radius: 2px;
    transition: 0.2s ease-out;
    position: relative;
    margin: 10px;
    left: 0px;
}

/*.card:not(:first-child) {*/
/*    margin-left: -50px;*/
/*}*/

.card:hover {
    transition: 0.2s ease-out;
    box-shadow: -3px 3px 3px #212121;
}

.card:hover ~ .card {
    /*left: 50px;*/
    transition: 0.2s ease-out;
}

.title {
    width: calc(100% - 7px);
    position: absolute;
    text-align: center;
    top: 10px;
    font-weight: 300;
    margin: 4px;
}

