.card:hover .filledbar {
    width: 100%;
    transition: 0.2s ease-out;
}

.circle {
    position: absolute;
    top: 100px;
    left: calc(39% - 59px);
}

.stroke {
    stroke: #ffffff;
    stroke-dasharray: 360;
    stroke-dashoffset: 360;
    transition: 0.3s ease-out;
}

.circle > svg {
    fill: #4a6262;
    stroke-width: 2px;
    width: 64px;
    height: 100px;
    transition: 0.1s ease-out;
}

svg:hover {
    cursor: pointer;
    fill: #F5A629;
}

.card:hover .stroke {
    stroke-dashoffset: 100;
    transition: 0.3s ease-out;
}


