.card {
    overflow: hidden;
}

.arrow-right {
    background-color: #444;
    box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.8);
    height: 100px;
    left: -50px;
    position: absolute;
    top: -50px;
    width: 100px;
    opacity: 0;
    cursor: pointer;
    -webkit-transform: rotate(-45deg);
    z-index: 1000;
}

.card:hover > .arrow-right {
    opacity: 1;
}

.arrow-right > h4 {
    color: #f5f5f5;
    font-family: sans-serif;
    font-size: 1.005em;
    left: 28px;
    top: 78px;
    opacity: 0;
    position: absolute;
    width: 80px;
}

.card:hover > .arrow-right h4 {
    opacity: 1;
}
